import {FaGithub, FaLinkedin, FaTwitter} from "react-icons/fa";
import {IconContext} from "react-icons";
import './Footer.css';

const Footer = () => {
    return(
        <IconContext.Provider value={{ color: "#DDDDDD", className: "iconProp", size: "6rem"}}>
  <div className="icons">
    <a href="https://twitter.com/jonesyt3ch">
    <FaTwitter />
    </a>
    <a href="https://www.linkedin.com/in/stephonxjones">
    <FaLinkedin />
    </a>
    <a href="https://www.github.com/poruvo">
    <FaGithub />
    </a>
  </div>
</IconContext.Provider>
     
    )
}

export default Footer