import './ContactFooter.css'
import React from 'react';
import emPic from './Pics/jonesyemailpic.png'



const ContactFooter = () => {
    return(
        <div className="contactDiv">
            <h1 className="contactText">Email me at:</h1>
            <img className="emailPic" src={emPic} alt="Jonesytech Email" />
        </div>
    )
}

export default ContactFooter